#root {
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0;
}

.round {
  border-radius: 100vmax;
}
.bordered {
  border: .25em solid #f60;
}
.middle-align {
  vertical-align: middle;
}
.our-header.our-header {
  color: #595959;
  text-align: left;
  border-left: .25em solid #f60;
  padding: .75em;
}

pre.prism-code {
  background: black;
}

.spectacle-content li {
  line-height: 1.5;
}
.spectacle-deck > button {
  color: #cbbcbd;
  opacity: .3;
}

@-webkit-keyframes shake {
	0% { -webkit-transform: translate(.2em, .1em) rotate(0deg); transform: translate(.2em, .1em) rotate(0deg); }
	10% { -webkit-transform: translate(-.1em, -.2em) rotate(-1deg); transform: translate(-.1em, -.2em) rotate(-1deg); }
	20% { -webkit-transform: translate(-.3em, 0) rotate(1deg); transform: translate(-.3em, 0) rotate(1deg); }
	30% { -webkit-transform: translate(0, .2em) rotate(0deg); transform: translate(0, .2em) rotate(0deg); }
	40% { -webkit-transform: translate(.1em, -.1em) rotate(1deg); transform: translate(.1em, -.1em) rotate(1deg); }
	50% { -webkit-transform: translate(-.1em, .2em) rotate(-1deg); transform: translate(-.1em, .2em) rotate(-1deg); }
	60% { -webkit-transform: translate(-.3em, .1em) rotate(0deg); transform: translate(-.3em, .1em) rotate(0deg); }
	70% { -webkit-transform: translate(.2em, .1em) rotate(-1deg); transform: translate(.2em, .1em) rotate(-1deg); }
	80% { -webkit-transform: translate(-.1em, -.1em) rotate(1deg); transform: translate(-.1em, -.1em) rotate(1deg); }
	90% { -webkit-transform: translate(.2em, .2em) rotate(0deg); transform: translate(.2em, .2em) rotate(0deg); }
	100% { -webkit-transform: translate(.1em, -.2em) rotate(-1deg); transform: translate(.1em, -.2em) rotate(-1deg); }
}

@keyframes shake {
	0% { -webkit-transform: translate(.2em, .1em) rotate(0deg); transform: translate(.2em, .1em) rotate(0deg); }
	10% { -webkit-transform: translate(-.1em, -.2em) rotate(-1deg); transform: translate(-.1em, -.2em) rotate(-1deg); }
	20% { -webkit-transform: translate(-.3em, 0) rotate(1deg); transform: translate(-.3em, 0) rotate(1deg); }
	30% { -webkit-transform: translate(0, .2em) rotate(0deg); transform: translate(0, .2em) rotate(0deg); }
	40% { -webkit-transform: translate(.1em, -.1em) rotate(1deg); transform: translate(.1em, -.1em) rotate(1deg); }
	50% { -webkit-transform: translate(-.1em, .2em) rotate(-1deg); transform: translate(-.1em, .2em) rotate(-1deg); }
	60% { -webkit-transform: translate(-.3em, .1em) rotate(0deg); transform: translate(-.3em, .1em) rotate(0deg); }
	70% { -webkit-transform: translate(.2em, .1em) rotate(-1deg); transform: translate(.2em, .1em) rotate(-1deg); }
	80% { -webkit-transform: translate(-.1em, -.1em) rotate(1deg); transform: translate(-.1em, -.1em) rotate(1deg); }
	90% { -webkit-transform: translate(.2em, .2em) rotate(0deg); transform: translate(.2em, .2em) rotate(0deg); }
	100% { -webkit-transform: translate(.1em, -.2em) rotate(-1deg); transform: translate(.1em, -.2em) rotate(-1deg); }
}
.shaking {
	-webkit-animation: shake .3s infinite linear;
	        animation: shake .3s infinite linear;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  will-change: transform;
}

@-webkit-keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
}

@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
}
.blinking {
  -webkit-animation: blink 1s steps(1) infinite;
          animation: blink 1s steps(1) infinite;
}

@-webkit-keyframes head {
  0% { -webkit-transform: rotate(3deg); transform: rotate(3deg); }
  50% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  100% { -webkit-transform: rotate(3deg); transform: rotate(3deg); }
}

@keyframes head {
  0% { -webkit-transform: rotate(3deg); transform: rotate(3deg); }
  50% { -webkit-transform: rotate(-10deg); transform: rotate(-10deg); }
  100% { -webkit-transform: rotate(3deg); transform: rotate(3deg); }
}
@-webkit-keyframes tentacles {
  0% { -webkit-transform: scaleY(1); transform: scaleY(1); }
  50% { -webkit-transform: scaleY(.97); transform: scaleY(.97); }
  100% { -webkit-transform: scaleY(1); transform: scaleY(1); }
}
@keyframes tentacles {
  0% { -webkit-transform: scaleY(1); transform: scaleY(1); }
  50% { -webkit-transform: scaleY(.97); transform: scaleY(.97); }
  100% { -webkit-transform: scaleY(1); transform: scaleY(1); }
}
.head.head {
  width: 42.3%;
  -webkit-animation: head 2s infinite;
          animation: head 2s infinite;
  -webkit-transform-origin: 50% 90%;
      -ms-transform-origin: 50% 90%;
          transform-origin: 50% 90%;
  margin-left: 35.5%;
  margin-bottom: -16%;
  position: relative;
  z-index: 10;
}
.tentacles.tentacles {
  width: 100%;
  -webkit-animation: tentacles 2s infinite;
          animation: tentacles 2s infinite;
  -webkit-transform-origin: 50% 100%;
      -ms-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
}



